<template>
<div class="fx-upload2">
    <header-text :headerText="headerTitle" @header-event="onHeaderBtn" class="fx-header"></header-text>
    <div class="fx-content">
        <div class="text">尊敬的投保人：<br/>感谢您的信任并投保我公司。<br/>您为被保人{证件号码为（证件号）}递交的投保单号<br/>码为（投保单号）的投保申请，我公司已经收悉。现<br/>因业务处理及审核工作中出现了如下情况需要与您进<br/>一步沟通。</div>
        <div class="subTitle">原因：（后台返回）</div>
        <div class="text">本内容涉及到投保单资料的确认，属于保险合同的组<br/>成部分。为使您的利益得到有效的保证，请您在收到<br/>本通知书后的（有效期）天内给与答复，并务必请您<br/>及（被保人）或其监护人在本通知书上亲笔签字。<br/></div>
        <div class="subTitle">1.返回补充类型</div>
        <up-load @return-img="getImg"></up-load>
    </div>
    <div class="fx-btns">
        <div class="nextBtn">确认提交</div>
    </div>
</div>
</template>
    
<script>
import Vue from "vue";
import headerText from "@/components/header";
import upLoad from "@/components/upload";
export default {
    name:'upload2',
    data(){
        return{
            headerTitle:'资料上传',
        }
    },
    created() {

    },
    methods: {
        //点击header
        onHeaderBtn(headerText){
            console.log(headerText);
        },
        //获取图片
        getImg(imgList){
            console.log('图片列表');
            console.log(imgList);
        },
    },
    computed:{},
    components:{
        "header-text":headerText,
        "up-load":upLoad
    },
    mounted(){
        
    }
}
</script>

<style lang="scss">
.fx-upload2{
    .fx-content{
        margin: r(20) 0;
        .text{
            font-size: r(30);
            line-height: r(44);
            color: #333;
            font-weight:lighter;
            margin: 0 r(15);
        }
        .subTitle{
            line-height:r(44);
            padding-left: r(15);
            font-size: r(32);
            margin:r(21) 0;
            font-weight:normal;
            color: #000;
        }
    }
    .fx-btns{
        height:r(92);
        position:fixed;
        bottom:0;
        width:r(750);
        border-top:1px solid #ddd;
        div{
            width:100%;
            height:100%;
            float:left;
            text-align: center;
            font-size:r(30);
        }
        .nextBtn{
            background:#0084ff;
            color:#fff;
            line-height:r(92);
        }
    }
}
</style>


